










import { Component, Vue, Emit } from 'vue-property-decorator';
import expressionList from '@/config/chat-expression';

@Component
export default class Expresssion extends Vue {
  expression: IObj<string> = {};
  mounted() {
    this.expression = expressionList;
  }
  @Emit()
  select(index: string) {
    return index;
  }
}
